
<template>
    <div class="layout">
        <Layout>
            <my-head @back="bacl"></my-head>
            <Content :style="{padding: '0 50px'}">
                <Row type="flex" justify="center" align="middle" class="code-row-bg">

                    <Col span="20" push="4" type="flex" justify="end">
                    <Row type="flex" justify="end" class="code-row-bg">
                        <span style="margin-right: 40px;font-size: 14px">单据号：{{head.associate_no}}</span>
                        <span style="margin-right: 40px;font-size: 14px">供应商：{{head.publisher_no}}</span>
                        <span style="margin-right: 20px;font-size: 14px" v-text="head.dt!=undefined?'制单时间：'+head.dt.split('T')[0]:''"></span>
                    </Row>
                    </Col>
                    <Col span="4" pull="20">
                    <Breadcrumb :style="{margin: '20px 0'}">
                        <BreadcrumbItem to="/Take">收货单</BreadcrumbItem>
                        <BreadcrumbItem>产品详情</BreadcrumbItem>
                    </Breadcrumb>
                    </Col>
                </Row>
                <Card style="padding: 0">
                    <div>
                        <Table @on-row-click="teblcli" :loading="lodaing" border stripe :columns="columns1"
                               :data="data1"
                               style="height: 520px;"></Table>
                    </div>
                    <Row type="flex" justify="center" align="middle" class="code-row-bg" style="margin-top: 16px;">
                        <Col span="8">
                        <router-link :to="{path:'TakePhoto',query:{associate_n:$route.query.associate_n}}"
                                     style="color: #fff;">
                            <Button type="warning" style="padding-left: 30px;padding-right: 30px;margin-right: 30px">
                                拍照留档
                            </Button>
                        </router-link>
                        <Button :disabled="disabled" type="primary" @click="toLoading2"
                                style="padding-left: 30px;padding-right: 30px;">
                            <span>确认收货</span>
                        </Button>
                        </Col>
                        <Col span="16">
                        <Row type="flex" justify="end" class="code-row-bg">
                            <Col style="margin: 4px 15px 0  0;">
                            共<span style="color: #D72236;font-size: 18px;font-weight: 700"> {{total}} </span>条数据，有<span
                                style="color: #D72236;font-size: 18px;font-weight: 700"> {{Untreated}} </span>条待上架产品
                            </Col>
                            <Page style="float: right;" @on-change="pagecli" :total="total" :page-size="10"></Page>

                        </Row>
                        </Col>
                    </Row>
                </Card>
            </Content>
            <Footer class="layout-footer-center" style="text-align: center">2011-2018 &copy; 怡乐仓库</Footer>
            <Modal
                    v-model="modal6"
                    :title="Title"
                    :loading="loading"
                    @on-ok="asyncOK">
                <div>
                    <Row type="flex" justify="center" align="middle" class="code-row-bg" style="margin-bottom: 16px;">
                        <Col span="4">
                        <Row type="flex" justify="end">实收数：</Row>
                        </Col>
                        <Col span="18">
                        <InputNumber :disabled="disabled" :step="1" v-model="value2" :min="0" style="width: 150px;"></InputNumber>
                        </Col>
                    </Row>
                    <Row type="flex" justify="center" align="middle" class="code-row-bg" style="margin-bottom: 16px;">
                        <Col span="4">
                        <Row type="flex" justify="end">差异原因：</Row>
                        </Col>
                        <Col span="18">
                        <CheckboxGroup v-model="fruit">
                            <template v-for="item in labels">
                            <Checkbox :label="item.reason" :disabled="disabled"></Checkbox>
                            </template>
                        </CheckboxGroup>
                        </Col>
                    </Row>
                    <Row type="flex" justify="center" align="middle" class="code-row-bg">
                        <Col span="4">
                        <Row type="flex" justify="end"></Row>
                        </Col>
                        <Col span="18">
                        <Input :disabled="disabled" :maxlength="100" v-model="value3" type="textarea" :rows="4"
                               placeholder="Enter something..."></Input>
                        </Col>
                    </Row>
                </div>
            </Modal>
            <Modal
                    v-model="modal1"
                    title="提示"
                    @on-ok="titlOk"
                    :loading="loading2">
                <p style="text-align: center;font-size: 13px;padding: 50px;">
                    <Icon type="alert" size="16" style="margin-right: 5px;"></Icon>
                    是否要将订单 <{{$route.query.associate_n}}> 确认收货，一旦提交将无法修改
                </p>
            </Modal>
        </Layout>
    </div>
</template>
<script>
    export default {
        name: 'TakeVou',
        data () {
            return {
                fruit: [],
                labels: [],
                user: {},
                total: 1,
                page: 0,
                head: '',
                Title: '',
                lodaing: true,
                value2: 0,
                value3: '',
                modal6: false,
                modal1: false,
                loading: true,
                loading2: true,
                serial: '',
                Untreated: '',
                columns1: [
                    {
                        title: '产品编号',
                        key: 'serial_no'
                    },
                    {
                        title: '名称',
                        key: 'book_name',
                        width: '20%'
                    },
                    {
                        title: '分类',
                        key: 'booktype_name'
                    },
                    {
                        title: '规格',
                        key: 'bind_name'
                    },
                    {
                        title: '单位',
                        key: 'book_propertyName'
                    },
                    {
                        title: '应收',
                        key: 'receivable_num'
                    },
                    {
                        title: '实收',
                        key: 'num1',
                        render: function (h, i) {
                            if (i.row.num1 == -0.1) {
                                return ''
                            } else {
                                return i.row.num1
                            }
                        }
                    },
                    {
                        title: '差异',
                        key: 'num',
                        render: function (h, a) {
                            if (a.row.num1 === -0.1) {
                                return ''
                            } else {
                                if (a.row.receivable_num == a.row.num1) {
                                    return <p class="ivu-icon ivu-icon-checkmark"
                                              style="cursor: pointer;font-size:14px"></p>
                                } else {
                                    return a.row.error_num
                                }
                            }

                        }
                    }
                ],
                data1: [],
                disabled: true,
            }
        },
        methods: {


            //差异原因列表
            getdifftxt () {
                var v = this;
                v.Axios.post('http://116.247.103.119/imt16webapi/currency/getdifftxt', v.Qs.stringify({
                    type: '进货验收'
                })).then(function (res) {
                    v.labels = res.data.data
                });
            },

            //提交入库
            titlOk(){
                var v = this;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/ConfirmInStock', v.Qs.stringify({
                    associate_no: v.$route.query.associate_n,
                    memo: '',
                    Consignee: v.user.work_no
                })).then(function (res) {
                    if (res.data.error != 0) {
                        v.$Message.error(res.data.errorMsg)
                    } else {
                        v.disabled = true;
                        v.modal1 = false;
                        v.$Message.success('提交成功');
                    }

                })
            },

            //提交入库弹窗
            toLoading2 () {
                var v = this;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/NextInStock', v.Qs.stringify({
                    associate_no: v.$route.query.associate_n
                })).then(function (res) {
                    if (res.data.error == 0) {
                        v.modal1 = true
                    } else {
                        v.$Message.config({
                            duration: 3
                        });
                        var arr = res.data.errorMsg.split('：')[1].split('、');
                        var tim = 0;
                        for (var i = 0; i < arr.length; i++) {
                            (function (se) {
                                setTimeout(function () {
                                    v.$Message.error(arr[se]);
                                }, tim);
                                tim = tim + 300;
                            }(i))
                        }
                    }
                }).catch(function () {
                    v.$Message.error('对方不想说话，并且向你抛出了一个异常');
                })
            },

            //产品列表
            InStock(num){
                var v = this;
                v.lodaing = true;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/GetInStockNewNoby', v.Qs.stringify({
                    associate_no: v.$route.query.associate_n,
                    pagenum: num,
                    pagesize: 10
                })).then(function (res) {
                    if (res.data.error != 0) {
                        v.$emit('error', String(res.data.errorMsg))
                    } else {
                        v.head = res.data.data.orpptb20;
                        v.data1 = res.data.data.resultorpptb22;
                        v.total = res.data.total;
                        v.lodaing = false;
                        v.Untreated = res.data.Untreated;
                        if (res.data.data.orpptb20.status == 1) {
                            v.disabled = false
                        } else {
                            v.disabled = true
                        }
                    }
                }).catch(function () {
                    v.$Message.error('对方不想说话，并且向你抛出了一个异常');
                })
            },

            //审核数量
            asyncOK () {
                var v = this;
                v.Axios.post('http://116.247.103.119/imt16webapi/sheets/SingleInStock', v.Qs.stringify({
                    associate_no: v.$route.query.associate_n,
                    serial_no: v.serial,
                    num1: v.value2,
                    memo: v.value3 + '@#$%~' + JSON.stringify(v.fruit) + ' ',
                    oper_no: v.user.member_no,
                    validity_dt: ''
                })).then(function (res) {
                    if (res.data.error != 0) {
                        v.$Message.error(res.data.errorMsg)
                        v.loading=false
                    } else {
                        v.InStock(v.page);
                        v.modal6 = false;
                        v.$Message.success('提交成功')
                    }
                    setTimeout(function () {
                        v.loading = true;
                    }, 0)
                });
            },

            teblcli(i){
                var v = this;
                if (i.num1 === -0.1) {
                    v.value2 = i.num
                } else {
                    v.value2 = i.num1;
                }
                if (i.Remarks != null) {
                    v.value3 = i.Remarks.split('@#$%~')[0];
                    if (i.Remarks.split('@#$%~')[1] !== undefined) {
                        v.fruit = JSON.parse(i.Remarks.split('@#$%~')[1])
                    } else {
                    }
                } else {
                    v.fruit = [];
                    v.value3 = '';
                }
                v.modal6 = true;
                v.serial = i.serial_no;
                v.Title = '审核数量 / ' + i.serial_no;

            },

            pagecli(i){
                this.page = i;
                this.InStock(this.page)
            },

            bacl(i){
                this.user = i
            }
        },
        mounted(){
            this.InStock(1);
            this.$emit('')
            this.getdifftxt()

        }
    }

</script>

<!-- Add "scoped" attribute to limit less to this component only -->
<style lang="less">
</style>



